:root {
  --height: 100vh;
  --width: 100vw;
  --percent: 100%;

  --grid: repeat(2, calc(var(--percent) / 2 - 9px));

  --size-1: calc(var(--height) / 40 + var(--width) / 71.1111);
  --size-2: calc(var(--height) / 50 + var(--width) / 88.8888);
  --size-3: calc(var(--height) / 95 + var(--width) / 168.8888);
  --size-4: calc(var(--height) / 100 + var(--width) / 177.7777);

  @media (min-width: 768px) {
    --grid: repeat(3, calc(var(--percent) / 3 - 46px));
  }

  @media (min-width: 1024px) {
    --grid: repeat(4, calc(var(--percent) / 4 - 51.75px));

    --size-1: calc(var(--height) / 50 + var(--width) / 88.8888);
    --size-2: calc(var(--height) / 70 + var(--width) / 124.4444);
  }
}
